import React from "react";
import { useSelector } from "react-redux";
import Plus from './Plus.js';
import Minus from './Minus.js';
import Reset from './Reset.js';

function App() {
  const counter = useSelector(state => state)
  return (
    <div>
      {counter}
      <div>
        <Plus/>
        <Minus/>
      </div>
      <Reset/>
    </div>
  )
  
}

export default App;
