import {createStore} from 'redux'


function reducer(state = 0, action){
    if(action.type === 'plus'){
        return state + 1;
    }

    if(action.type === 'minus'){
        if(state <= 0 ){ return state = 0}return state -1;
    } 

    if(action.type === 'reset'){
        return state = 0;
    }

    return state;
}

const store = createStore(reducer);
export default store;
